*{
    margin: 0;
    padding: 0;
  }
  body{
    background-color: azure;
  }
  .app{
    text-align: center;
    color: blue;
  }
  h1{
    color: green;
  }
  .app input{
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid gray;
    outline: none;
    padding: 10px 5px;
    width: 200px;
  }
  .app button{
    background-color: blueviolet;
    color: white;
    border: none;
    outline: none;
    margin-top: 10px;
    padding: 10px 30px;
    font-size: 16px;
    cursor: pointer;
  }
  .navActive{
    color: chartreuse;
    font-size: 30px;
    font-weight: bold;
  }
  nav{
    background-color: blueviolet;
    display: flex;
    justify-content: center;
    margin-top: 5px;
    
  }
  nav ul{
    display: flex;
    list-style-type: none;
    align-items: center;
    justify-content: space-between;
    width: 40%;
    color: black;
    font-weight: bold;
    padding: 10px;
  }
  nav ul li{
    cursor: pointer;
    font-size: 14px;
    transition: all 0.4s ease-in-out;
  }
  nav ul li:hover{
    transform: scale(1.2);
  }